@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&family=Noto+Sans+JP&family=Noto+Sans+KR&display=swap');

* {
  font-family: 'Noto Sans KR', 'Noto Sans JP', 'Noto Sans',sans-serif;
  font-size: calc(1em + 1vw);
  box-sizing: border-box;
}

body {
  margin: 0px;
}

#root {
  display: flex;
  justify-content: center;
}

.App {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 20vh 1fr 10vh;
  grid-template-columns: repeat(4, 1fr);
}

.Header {
  font-family: 'Noto Sans KR', sans-serif;
  grid-column: 1 / span 4;
  background-color: aqua;
}

.Header .title {
  font-size: 100%;
}

.Aside {
  grid-column: 1 span;
  background-color: bisque;
}

.Main {
  grid-column: 2 / span 3;
  background-color: chartreuse;
}

.Footer {
  grid-column: 1 / span 4;
  background-color: cornflowerblue;
}

@media (max-width: 700px) {
  .App {
    grid-template-rows: 20vh 10vh 1fr 10vh;
  }
  .Main ,
  .Aside {
    grid-column: 1 / span 4;
  }
}